
import Swiper from 'vux/src/components/swiper/swiper.vue'

export default {
name: "activegood",
data () {
return {
showbox:false,
goodsnum:0,
active:[]
}
},
mounted:function () {
this.$axios.get('/find/activity_detail?activity_id=5&user_id=1').then(res=>{
console.log(res);
this.active=res.data.data;
console.log(this.active.banner_img[0]);
})
},
methods:{
goodshow(){
this.showbox=!this.showbox;

},
close(){
this.showbox=false;

},
add(){
this.goodsnum++
},
reduce(){
this.goodsnum--;
if(this.goodsnum<=0){
this.goodsnum=0;
}
},

},
components: {
Swiper
}
}



